/* global document, Office */

Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("run").onclick = run;
  }
});

import "core-js";

export async function run() {
  Office.context.ui.displayDialogAsync(
    "https://localhost:3000/dialog.html",
    { height: 75, width: 50, displayInIframe: true },
    (asyncResult) => {
      const dialog = asyncResult.value;
      dialog.addEventHandler(Office.EventType.DialogMessageReceived, (arg) => {
        dialog.close();
        // eslint-disable-next-line no-undef
        processMessage(arg);
      });
    }
  );
  var toLength = 0;
  const item = Office.context.mailbox.item;
  var attachmentsLength = -1;
  if (item.to !== undefined) {
    toLength = item.to.length;
  }
  if (item.attachments !== undefined) {
    attachmentsLength = item.attachments.length;
  }
  var tos = "";
  for (var i = 0; i < toLength; i++) {
    tos = tos + item.to[i].emailAddress + ";";
  }
  tos = tos.substring(0, tos.length - 1);
  let insertAt = document.getElementById("item-subject");
  let label1 = document.createElement("b").appendChild(document.createTextNode("From: "));
  insertAt.appendChild(label1);
  insertAt.appendChild(document.createElement("br"));
  insertAt.appendChild(document.createTextNode(item.from.emailAddress));
  insertAt.appendChild(document.createElement("br"));
  let label2 = document.createElement("b").appendChild(document.createTextNode("To: "));
  insertAt.appendChild(label2);
  insertAt.appendChild(document.createElement("br"));
  insertAt.appendChild(document.createTextNode(tos));
  insertAt.appendChild(document.createElement("br"));
  let label3 = document.createElement("b").appendChild(document.createTextNode("Subject: "));
  insertAt.appendChild(label3);
  insertAt.appendChild(document.createElement("br"));
  insertAt.appendChild(document.createTextNode(item.subject));
  insertAt.appendChild(document.createElement("br"));
  let bodyObject = {};
  Office.context.mailbox.item.body.getAsync("text", function (result) {
    if (result.status === Office.AsyncResultStatus.Succeeded) {
      bodyObject.body = result.value;
      let label4 = document.createElement("b").appendChild(document.createTextNode("Body: "));
      insertAt.appendChild(label4);
      insertAt.appendChild(document.createElement("br"));
      insertAt.appendChild(document.createTextNode(bodyObject.body));
      insertAt.appendChild(document.createElement("br"));
      for (i = 0; i < attachmentsLength; i++) {
        var nombre = item.attachments[i].name;
        let label5 = document.createElement("b").appendChild(document.createTextNode("Attachments: "));
        insertAt.appendChild(label5);
        insertAt.appendChild(document.createElement("br"));
        insertAt.appendChild(document.createTextNode(nombre));
        insertAt.appendChild(document.createElement("br"));
        item.getAttachmentContentAsync(item.attachments[i].id, function (result) {
          switch (result.value.format) {
            case Office.MailboxEnums.AttachmentContentFormat.Base64:
              {
                let label6 = document.createElement("b").appendChild(document.createTextNode("Attachments_Content: "));
                insertAt.appendChild(label6);
                insertAt.appendChild(document.createElement("br"));
                insertAt.appendChild(document.createTextNode(result.value.content.toString()));
                insertAt.appendChild(document.createElement("br"));
                let label7 = document.createElement("b").appendChild(document.createTextNode("SOAP Response: "));
                insertAt.appendChild(label7);
                insertAt.appendChild(document.createElement("br"));
                insertAt.appendChild(document.createTextNode("TEST"));
                insertAt.appendChild(document.createElement("br"));
              }

              break;
            case Office.MailboxEnums.AttachmentContentFormat.Eml:
              // Handle email item attachment.
              break;
            case Office.MailboxEnums.AttachmentContentFormat.ICalendar:
              // Handle .icalender attachment.
              break;
            case Office.MailboxEnums.AttachmentContentFormat.Url:
              // Handle cloud attachment.
              break;
            default:
            // Handle attachment formats that are not supported.
          }
        });
      }
    }
  });
}
